/* tab */
.react-tabs {
  margin-top: 24px;
}
.react__tab-content {
  padding-bottom: 20px;
  width: 100%;
  background: #212738;
}
.react-tabs__tab-list {
  background: #ffffff;
  box-shadow: 0px 0px 1px #e2e8f0;
  border-radius: 13px;
  padding: 24px;
  max-width: 100%;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  height: fit-content;
}
.faq__tab-content {
  display: flex;
  flex-wrap: wrap;
}
.react-tabs-panel {
  display: flex;
  column-gap: 16px;
}
.react-tabs__tab--selected {
  background: #fce34d;
  border-radius: 8px;
  font-weight: 500 !important;
  font-size: 23px;
  line-height: 28px;
  padding: 12px;
  text-align: center;
  letter-spacing: -0.02em;
  color: #000000 !important;
  transition: all 0.3s;
}
.react-tabs__tab {
  color: #b8c0cc;
  font-size: 23px;
  text-align: center;
  transition: all 0.3s;
  cursor: pointer;
  font-weight: 200;
  margin: 0 auto 38px;
}
.react-tabs__tab:last-child {
  margin-bottom: 0;
}

