.searchIcon {
  position: relative;
}

.dateSearch {
  position: relative;
  height: 30px;
  overflow: visible;
}
.cryptoIcon {
  position: relative;
  height: 30px;
  overflow: visible;
}

.dateSearch:before {
  content: "";
  position: absolute;
  top: -19%;
  left: -30px;
  width: 24px;
  height: 24px;
  background: url("../../src/assets/images/state/calendar.svg");
  z-index: 2;
}

.cryptoIcon:before {
  content: "";
  position: absolute;
  top: -19%;
  left: -30px;
  width: 24px;
  height: 24px;
  background: url("../../src/assets/images/statistic/bitcoin-refresh.svg");
  z-index: 2;
}
.searchIcon::before {
  content: "";
  position: absolute;
  top: 28%;
  right: 17px;
  width: 24px;
  height: 24px;
  background: url("../../src/assets/images/state/search.svg");
  z-index: 2;
}

/* tab */
.MuiTabs-flexContainer {
  flex-direction: column !important;
  align-items: center;
}
.MuiTabs-scrollButtons {
  display: flex !important;
}
.PrivateTabIndicator-colorSecondary-45 {
  display: none !important;
}
.css-19kzrtu {
  padding: 0 !important;
}
.swiper-wrapper.disabled {
  transform: translate3d(0px, 0, 0) !important;
}
.swiper-pagination.disabled {
  display: none;
}

.game {
  width: 100%;
  height: 200px;
  border: 1px dashed #fce34d;
  margin: 40px auto;
}

.dino {
  width: 50px;
  height: 50px;
  background-image: url("../assets/images/state/dollarCoin.webp");
  background-size: 50px 50px;
  position: relative;
  top: 150px;
}

.jump {
  animation: jump 0.3s linear;
}

@keyframes jump {
  0% {
    top: 150px;
  }

  30% {
    top: 130px;
  }

  50% {
    top: 80px;
  }

  80% {
    top: 130px;
  }

  100% {
    top: 150px;
  }
}

.cactus {
  width: 20px;
  height: 40px;
  position: relative;
  top: 110px;
  left: 50%;

  background-image: url("../assets/images/state/dollarCoin.webp");
  background-size: 20px 40px;

  animation: block 1s infinite linear;
}

@keyframes block {
  0% {
    left: 580px;
  }

  100% {
    left: -20px;
  }
}

.timer__items {
  display: flex;

  font-size: 48px;
  color: #000000;
}
.timer__item {
  position: relative;
  min-width: 60px;
  margin-left: 10px;
  margin-right: 10px;
  padding-bottom: 15px;
  text-align: center;
}

.timer__item:first-child {
  margin-left: 0;
}
.timer__item::before {
  content: attr(data-title);
  display: block;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  font-size: 14px;
}
.timer__item:not(:last-child)::after {
  content: ":";
  position: absolute;
  right: -15px;
}
